	module.exports = {"data":{"markdownRemark":{"html":"<h2>Github repository settings</h2>\n<p>Documentation on <a href=\"https://docs.github.com/en/actions/monitoring-and-troubleshooting-workflows/enabling-debug-logging\">Debug logging</a></p>\n<p><code>ACTIONS_RUNNER_DEBUG</code>: boolean<br>\n<code>ACTIONS_STEP_DEBUG</code>: boolean  </p>\n<h2>Environment variables available in github actions:</h2>\n<p><code>$GITHUB_SHA</code><br>\n<code>$GITHUB_REPOSITORY</code><br>\n<code>$GITHUB_WORKSPACE</code>  </p>\n<p>Exhaustive list of github actions environement variables is\navailable at <a href=\"https://docs.github.com/en/actions/learn-github-actions/environment-variables\">this link</a></p>","frontmatter":{"title":"Github","date":"July 15, 2022","path":"/github","tags":["github"],"excerpt":"Github"}}},"pathContext":{"prev":{"html":"","id":"/home/nicolas/projects/workshop/src/pages/2022-07-15-english-idioms/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-07-15T20:24:43.180Z","path":"/idioms","title":"English idioms","excerpt":"","tags":["idioms"]}},"next":{"html":"<h2>Deleting a commit</h2>\n<p>The --soft flag ensures changes from the deleted commit remain staged:</p>\n<pre><code class=\"language-zsh\">git reset --soft HEAD~1\n</code></pre>\n<h2>Rebasing to the tip of local main branch</h2>\n<pre><code class=\"language-zsh\">git rebase main\ngit push --force-with-lease\n</code></pre>\n<p>In case there are conflict execute commands like <code>git add ...</code> and <code>git rebase --continue</code></p>\n<p>When relevant, priority can be given to feature branch changes with:</p>\n<pre><code class=\"language-zsh\">git rebase --main -Xtheirs\n</code></pre>\n<p>followed with:</p>\n<pre><code class=\"language-zsh\">git push --force\n</code></pre>\n<h2>Updating the local branch with respect to remote</h2>\n<pre><code class=\"language-zsh\">git pull --rebase\n</code></pre>\n<h2>Interactive rebase</h2>\n<pre><code class=\"language-zsh\">git log --oneline --decorate --all --graph\n</code></pre>\n<pre><code class=\"language-zsh\">git rebase --interactive HEAD~3\n</code></pre>\n<pre><code class=\"language-zsh\">git push --force origin feat/add-my-feature\n</code></pre>\n<h2>Get back to older commit after several commits pushed to the remote</h2>\n<p>Use <code>git log</code> and <code>git reflog</code> to identify the\ncommit to target and get its sha1, say COMMIT_HASH</p>\n<p>Get back to that commit with:</p>\n<pre><code class=\"language-zsh\">git reset --hard COMMIT_HASH\n</code></pre>\n<p>Finally, push with:</p>\n<pre><code class=\"language-zsh\">git push origin feat/opensearch_operator --force-with-lease\n</code></pre>","id":"/home/nicolas/projects/workshop/src/pages/2022-08-20-git/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-08-20T23:16:22.342Z","path":"/git","title":"Git","excerpt":"","tags":["git","devops"]}}}}

